.App {
  text-align: center;
}



body{
  background-color: #051429;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}
#logo{
  position: absolute;
  z-index: 1000;
  color: white;
  text-align: center;
  width: 100%;
  font-weight: bold;
  font-size: 2.3rem;
  padding-top: 50px;
}

.ant-layout-header{
  padding:0 !important;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
